<template>
    <div
        :class="alert"
        class="message-box rounded text-white p-3 border font-light my-8">
        {{ message }}
    </div>
</template>

<script>
import { computed } from 'vue';

export default {
    name: 'AlertBox',
    props: {
        alertType: {
            type: String,
            default: 'info'
        },

        message: {
            type: String,
            default: 'Message Box'
        }
    },

    setup(props) {
        const alert = computed(() => {
            switch(props.alertType) {
                case 'info':
                    return 'bg-blue-400'
                    break;

                case 'success':
                    return 'bg-green-400'
                    break;

                case 'error':
                    return 'bg-red-400'
                    break;

                case 'warning':
                    return 'bg-yellow-400'
                    break;
            }
        })

        return {
            alert
        }
    }
}
</script>

